.headerMainInfo {
    margin-top: 30px;
    margin-bottom: 30px;
    display: flex;
    gap: 10px;
    justify-content: center;    
    align-items: center;
    @media screen and (max-width: 770px) {
        flex-direction: column;
        align-items: flex-start;
        margin-left: 20px;
    }
}

.title {
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: 400;
    background-color: rgba(255, 249, 255, 0.72);
    border-radius: 10px;
    box-shadow: 10px 10px 10px rgba(186, 179, 186, 0.8);
    padding: 10px; 
}

.subtitle {
    font-weight: 700;
}



.isLoading {
    display:flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.isError {
    display:flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    flex-direction: column;   
}

.selectMap div{
    width: 23px;
    height: 3px;
    padding: 0px !important;
    border: none;
    min-width: 0 !important;
    background-color: rgb(247, 247, 247) !important;
}

.main {    
    display:flex;
    margin: 10px;
    justify-content: center;    
    gap: 50px;
    flex-wrap: wrap;
    @media screen and (max-width: 800px) {
        flex-direction: column;
        align-items: center;
    }     
}


.teamContainer {
    display: flex;
    gap: 50px;    
    justify-content: center;
    flex-wrap: wrap;
    @media screen and (max-width: 1300px) {
        order: -1;
    }    
    @media screen and (max-width: 800px) {        
        margin: auto;        
        width: 300px;
        
    }
}

.leftContainer {
    display: flex;
    flex-direction: column;
    
}

.link {
    text-decoration: none;
    color: rgb(102, 72, 32);
    :hover {
        cursor: pointer
    }
}

