.isLoading {
    display:flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.isError {
    display:flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    flex-direction: column;   
} 

.container {
    padding: 10px;
    width: 100%;
    border: 1px solid rgb(182, 181, 181);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: auto;
    min-width: 300px;
    width: 300px;
}

.buttonsmainContainer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    gap: 5px;
}

.inputContainer {
    display: flex;
    /* flex-direction: column; */
    gap: 5px;
    /* width: 50%; */
    margin: 10px;
    justify-content: center;
}

.banInputContainer {
    display: flex;
    flex-direction: column;
    gap: 5px;
    
}


.link {
    text-decoration: none;
    :hover {
        cursor: pointer
    }
}